// В этом файле должны быть стили для БЭМ-блока cart, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.cart {

  $block-name:                &; // #{$block-name}__element

  &__inner {
    max-width: 1230px;
    padding: 0 10px;
    margin: auto;

    @media (min-width: $screen-md) {
      padding-right: 15px;
      padding-left: 15px;
    }
  }

  &__header {
    padding: 20px 0;

    @media (min-width: $screen-md) {
      display: flex;
      align-items: center;
      padding-top: 45px;
      padding-bottom: 54px;
    }
  }

  &__title {
    margin: 0 0 10px;
    font-size: $font-size--h3;
    font-weight: 900;

    @media (min-width: $screen-md) {
      margin: 0;
      padding-right: 10px;
      font-size: $font-size--h2;
    }
  }

  &__clear-btn {

    @media (min-width: $screen-md) {
      margin-left: auto;
    }
  }

  &__table {
    width: 100%;
    border-collapse: collapse;

    @media (max-width: ($screen-md - 1)) {
      display: block;
    }

    tr:not(:last-child) {

      @media (min-width: $screen-md) {
        border-bottom: 1px solid $gray-lighten;
      }
    }

    td {

      @media (max-width: ($screen-md - 1)) {
        display: block;
      }
    }

    .field-num {
      margin-bottom: 0;
    }
  }

  &__table-headers {
    font-size: $font-size;
    font-weight: 500;

    @media (max-width: ($screen-md - 1)) {
      display: none;
    }

    th {
      padding-bottom: 10px;
      font: inherit;

      &.cart__col-2 {

        @media (min-width: $screen-lg) {
          padding-left: 25px;
        }
      }
    }
  }

  &__product {

    @media (max-width: ($screen-md - 1)) {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 20px;
    }
  }

  &__col-1 {

    @media (max-width: ($screen-md - 1)) {
      width: 60px;
      flex-shrink: 0;
    }

    @media (min-width: $screen-md) {
      width: 110px;
      padding: 6px 0;
    }

    img {
      display: block;
    }
  }

  &__col-2 {

    @media (max-width: ($screen-md - 1)) {
      width: calc(100% - 60px);
      align-self: center;
      padding-left: 10px;
    }

    @media (min-width: $screen-md) {
      padding: 6px 20px 6px 30px;
    }

    @media (min-width: $screen-lg) {
      padding-left: 78px;
    }
  }

  &__col-3 {

    @media (max-width: ($screen-md - 1)) {
      order: 10;
      align-self: center;
      margin-left: auto;
    }

    @media (min-width: $screen-md) {
      width: 120px;
      text-align: center;
    }

    @media (min-width: $screen-lg) {
      width: 200px;
    }

    .field-num {
      display: inline-block;
    }
  }

  &__col-4 {

    @media (max-width: ($screen-md - 1)) {
      align-self: center;
    }

    @media (min-width: $screen-md) {
      width: 120px;
      padding: 0 10px;
      text-align: center;
    }

    @media (min-width: $screen-lg) {
      width: 160px;
    }
  }

  &__col-5 {

    @media (min-width: $screen-md) {
      width: 60px;
      text-align: center;
    }

    @media (min-width: $screen-lg) {
      width: 100px;
    }

    @media (min-width: $screen-xl) {
      text-align: left;
    }
  }

  &__item-name {
    font-size: $font-size;
    line-height: 1.5;
    font-weight: 700;
  }

  &__item-price {
    font-size: $font-size;
    font-weight: 700;
    white-space: nowrap;
  }

  &__product-del-btn.close {
    position: absolute;
    top: 0;
    left: 30px;
    background: rgba(255, 255, 255, 0.7);

    @media (min-width: $screen-md) {
      position: static;
      background-color: transparent;
      transform: translateY(-0.2em);

      &:hover,
      &:focus {

        svg {
          fill: $text-color;
        }
      }

      svg {
        fill: $gray-lighten;
        transition: all 0.3s;
      }
    }
  }

  &__products-price {
    text-align: right;

    @media (min-width: $screen-md) {
      padding-top: 20px;
      padding-right: 30px;
      font-size: $font-size--h3 + 2;
    }
  }

  &__products-price-num {
    display: inline-block;
    margin-left: 0.3em;
    font-weight: 900;
  }

  &__checkout {
    position: relative;
    max-width: 694px;
    margin: auto;
  }

  &__courier-data-right {

    @media (min-width: $screen-md) {
      display: flex;
      flex-wrap: wrap;
      margin-left: -15px;
      margin-right: -15px;

      .field-select {
        width: 42%;
        padding: 0 15px;
      }

      .field-text {
        width: 58%;
        padding: 0 15px;
      }

      #{$block-name}__delivery-comment {
        width: 100%;
        max-width: 390px;
      }

      .field-select-group {
        width: 528px;
        padding: 0 15px;

        .field-select { // stylelint-disable-line
          width: auto;
          padding: 0;
        }
      }
    }
  }

  &__courier-data-left {
    font-size: $font-size + 2;

    @media (min-width: $screen-xl) {
      position: absolute;
      top: 240px;
      right: -280px;
      width: 220px;
    }
  }

  &__delivery {

    &--hidden {
      display: none;
    }
  }

  &__delivery-date {
    white-space: nowrap;
  }

  &__promocode {
    max-width: 358px;
  }
}
