// В этом файле должны быть стили для БЭМ-блока checkout, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.checkout {

  $block-name:                &; // #{$block-name}__element

  &__title {
    margin: 0 0 20px;
    font-size: $font-size--h3;
    font-weight: 900;

    @media (min-width: $screen-md) {
      margin: 40px 0;
      font-size: $font-size--h2 + 2;
    }
  }

  &__user-data {

    @media (min-width: $screen-md) {
      display: flex;
      flex-wrap: wrap;
      margin-right: -15px;
      margin-left: -15px;

      .field-text {
        width: 50%;
        padding: 0 15px;
      }
    }
  }

  &__discount {
    font-size: $font-size--sm;
  }

  &__discount-num {
    display: inline-block;
    margin-left: 25px;
    font-size: $font-size + 2;
    font-weight: 700;
  }

  &__all-price {
    font-size: $font-size--h3;

    @media (min-width: $screen-md) {
      font-size: $font-size--h2 + 2;
      line-height: 1;
      margin-bottom: 34px;
    }
  }

  &__price {
    display: inline-block;
    margin-left: 0.3em;
    font-weight: 900;
  }

  &__conditions {
    margin: 14px 0;
    max-width: 300px;
    font-size: $font-size--xs;

    @media (min-width: $screen-md) {
      margin-bottom: 60px;
    }
  }
}
