// Базовые цвета https://colorscheme.ru/color-converter.html

$black:                       hsl(0, 0%, 0%);
$gray-darkest:                #202124;
$gray-darker:                 hsl(0, 0%, 20%);
$gray-darken:                 #606060;
$gray-dark:                   #787878;
$gray:                        #a8a8a8;
$gray-light:                  #bfbfbf; // #c3c3c3  #acacac
$gray-lighten:                #d6d6d6; // неактивная кнопка
$gray-lighter:                #e5e5e5;
$gray-lighter-2:              #f6f6f6;
$gray-lightest:               #fafafa;
$white:                       hsl(0, 0%, 100%);

$color-main:                  #ff6b2b;
$color-main--light:           #ff8550;
$color-main--dark:            #cb500a;
$color-success:               #3fdd00;
$color-danger:                #f63f45;
$color-warning:               #2f75ec;



// Семантические цвета

$text-color:                  $gray-darkest;
$text-color--muted:           $gray-light;
$body-bg:                     $gray-lightest;

$link-color:                  $color-main;
$link-color--hover:           $color-main;

$border-color:                $gray-lighter;



// Базовая типографика

$font-size:                   16px;

$font-size--h1:               40px;
$font-size--h2:               32px;
$font-size--h3:               20px;
$font-size--h4:               18px;
$font-size--h5:               16px;
$font-size--h6:               16px;

$font-size--sm:               14px;
$font-size--xs:               12px;

$line-height:                 1.63;

$typo-margin-vertical:        1.8em;

$font-family:                 Montserrat, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-family--monospace:      SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
// Georgia, Times New Roman, Times, serif



// Ширины

$screen-xs:                   0;
$screen-sm:                   480px;
$screen-md:                   768px;
$screen-lg:                   992px;
$screen-xl:                   1280px;

$container-sm:                100%;
$container-md:                100%;
$container-lg:                $screen-lg - 30;
$container-xl:                1179px;



// Модульная сетка

$grid-columns:                12;
$grid-gutter-width:           30px;



// Разное

$field-padding-vertical:      14px;
$field-padding-horizontal:    24px;

$border-radius:               6px;
$opacity:                     0.5;
$transition-time:             0.3s;
$shadow:                      0 6px 24px 0 rgba(0, 0, 0, 0.08);

$menu-desktop-width:          $screen-lg;
$toggler-part-height:         3px;
$toggler-part-bg-color:       #000;

$z-index-modal:               100;
