// В этом файле должны быть стили для БЭМ-блока tabs, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.tabs {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: $font-size;
  line-height: 1;

  &__item {
    margin-bottom: 5px;
  }

  &__item-link {
    position: relative;
    z-index: 1;
    display: inline-block;
    padding: 0.5em 10px;
    color: inherit;

    @media (min-width: $screen-md) {
      padding-left: 15px;
      padding-right: 15px;
    }

    &:before {
      content: '';
      position: absolute;
      z-index: -1;
      right: 10px;
      bottom: 0.5em;
      left: 7px;
      height: 4px;
      background-color: $color-main;
      opacity: 0;
      transition: all 0.3s;

      @media (min-width: $screen-md) {
        left: 12px;
        right: 15px;
      }
    }

    #{$block-name}__item--active &,
    &:hover,
    &:focus {
      color: inherit;
      text-decoration: none;

      &:before {
        opacity: 1;
      }
    }
  }
}
