// В этом файле должны быть стили для БЭМ-блока rating, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.rating {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  align-items: center;
  font-size: $font-size--sm;

  svg {
    margin-right: 4px;
  }

  &__stars {
    display: inline-flex;
  }

  &__num {
    margin-right: 22px;
    margin-left: 10px;
  }

  &__review {
    white-space: nowrap;
    color: $gray;
  }
}
