// В этом файле должны быть стили для БЭМ-блока field-select-group, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field-select-group {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: $typo-margin-vertical;

  &__name {
    @include field-name;
    cursor: default;
  }

  &__wrap {
    display: flex;

    .field-select {
      flex-grow: 1;
      margin-bottom: 0;

      &:first-child {

        .choices__inner { // stylelint-disable-line
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
      }

      &:last-child {

        .choices__inner { // stylelint-disable-line
          border-left: 0;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }
      }
    }
  }



  &--sm {
    margin-bottom: 1em;

    @media (min-width: $screen-sm) {
      display: flex;
      align-items: baseline;
    }

    #{$block-name}__name {
      color: $gray;
      font-size: $font-size--xs;
      font-weight: 500;
      text-transform: none;

      @media (min-width: $screen-sm) {
        flex-shrink: 0;
        width: 115px;
        margin-bottom: 0;
      }
    }

    #{$block-name}__wrap {

      @media (min-width: $screen-sm) {
        flex-grow: 1;
      }
    }
  }
}
