// В этом файле должны быть стили для БЭМ-блока social, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    margin-left: 15px;
    color: #fff;
    text-decoration: none;

    &:hover,
    &:focus {
      color: #fff;
      text-decoration: none;
    }

    svg {
      display: block;
    }

    &--inst {
      background: linear-gradient(to bottom, #7024c4, #c21975 42%, #c74c4d 70%, #e09b3d);
    }

    &--fb {
      background: #3d5a96;
    }

    &--tw {
      background: #2aa3ef;
    }
  }
}
