// В этом файле должны быть стили для БЭМ-блока card, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.card {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 5px;
  margin-bottom: 60px;
  text-align: center;

  @media (min-width: $screen-sm) {
    width: 220px;
  }

  &:hover,
  &:focus {
    z-index: 1;
  }

  .no-touch &__inner {

    &:before { // stylelint-disable-line
      content: '';
      position: absolute;
      z-index: -1;
      top: -24px;
      right: 0;
      bottom: -90px;
      left: 0;
      border: 1px solid $gray-lighter;
      background-color: #fff;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
      opacity: 0;
      transform: scale(0.4);
      transition: all 0.3s;
    }
  }

  .no-touch &:hover {

    #{$block-name}__inner:before { // stylelint-disable-line
      transform: scale(1);
      opacity: 1;
    }

    #{$block-name}__buy { // stylelint-disable-line
      opacity: 1;
      transform: translateX(-50%) scale(1);
      transition: all 0.15s 0.3s;
    }
  }

  &__inner {
    flex-grow: 1;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-bottom: 20px;
    color: $text-color;
    text-decoration: none;

    &:hover,
    &:focus {
      color: $text-color;
      text-decoration: none;
    }
  }

  &__title {
    flex-grow: 1;
    max-width: 210px;
    margin: 0 0 15px;
    font-size: $font-size--h4;
    line-height: 1.28;
    font-weight: 400;
  }

  &__img {
    flex-shrink: 0;
    display: block;
    margin: 0 auto 30px;
  }

  &__new {
    position: absolute;
    top: -18px;
    left: 50%;
    width: 52px;
    height: 52px;
    border-radius: 50%;
    font-size: $font-size--sm;
    line-height: 50px;
    font-weight: 700;
    color: #fff;
    background: $color-success;
    transform: translateX(32px) rotate(-10deg);
  }

  &__price {
    flex-shrink: 0;
    margin-bottom: 0;
    font-size: $font-size--h3 + 2;
    font-weight: 700;
    line-height: 1;
  }

  &__buy {
    flex-shrink: 0;

    .no-touch & {
      position: absolute;
      z-index: 2;
      bottom: -48px;
      left: 50%;
      opacity: 0;
      transform: translateX(-50%) scale(0.3);
      transition: all 0.15s;
    }
  }
}
