// В этом файле должны быть стили для БЭМ-блока field-select, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field-select {

  $block-name:                &; // #{$block-name}__element

  display: block;
  margin-bottom: $typo-margin-vertical;

  &__name {
    @include field-name;
    cursor: default;
  }

  &__help-text {
    @include field-help-text;
  }

  [class^='field-select__lang-'] {
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 14px;
    margin-right: 0.4em;
    border: 1px solid $gray-lighter;
    background-position: center;
    background-size: 20px 20px;
  }

  &__lang-ru {
    background-image: url(./src/blocks/field-select/bg-img/flag-ru.png);
  }

  &__lang-en {
    background-image: url(./src/blocks/field-select/bg-img/flag-en.png);
  }



  &--labeled {
    width: 100%;

    @media (min-width: $screen-sm) {
      display: inline-flex;
      align-items: baseline;
      position: relative;
      width: auto;
      border: 1px solid $border-color;
      border-radius: $border-radius;
      padding-left: $field-padding-horizontal;

      #{$block-name}__name {
        position: relative;
        z-index: 1;
        margin-right: 30px;
        margin-bottom: 0;
        font-size: $font-size--sm;
        font-weight: 500;
        color: $gray-light;
        text-transform: none;
      }

      .choices {
        position: static;
      }

      .choices__inner {
        border: 0;

        &:before {
          content: '';
          position: absolute;
          z-index: 0;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          border-radius: $border-radius;
        }

        &:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
        }
      }

      .choices__list--dropdown {
        right: 0;
      }

      .choices__item {
        position: relative;
        z-index: 1;
      }
    }
  }

  &--sm {
    margin-bottom: 1em;

    @media (min-width: $screen-sm) {
      display: flex;
      align-items: baseline;
    }

    &#{$block-name}--labeled {

      @media (min-width: $screen-sm) {
        padding-left: 14px;
      }
    }

    #{$block-name}__name {
      color: $gray;
      font-size: $font-size--xs;
      font-weight: 500;
      text-transform: none;

      @media (min-width: $screen-sm) {
        flex-shrink: 0;
        width: 115px;
        margin-bottom: 0;
        font-size: $font-size--xs;
      }
    }

    &#{$block-name}--labeled #{$block-name}__name {
      margin-right: 0;
    }

    #{$block-name}__select-wrap {

      @media (min-width: $screen-sm) {
        flex-grow: 1;
      }
    }

    .choices {
      font-size: $font-size--xs;

      &:after {
        top: 10px;
        right: 17px;
      }
    }

    .choices__inner {
      padding: 9px 35px 9px 14px;
    }

    .choices__list--dropdown .choices__item { // stylelint-disable-line
      padding: 15px 14px;
    }
  }

  &--lang {

    #{$block-name}__name {

      @media (min-width: $screen-md) {
        text-align: right;
        padding-right: 6px;
      }

      @media (min-width: $screen-lg) {
        flex-grow: 1;
        width: 85px;
      }

      @media (min-width: $screen-xl) {

        .filters__list-data & {
          width: 46px;
        }
      }
    }

    #{$block-name}__select-wrap {

      @media (min-width: $screen-lg) {
        max-width: 107px;
      }
    }

    .choices__list--dropdown {

      @media (min-width: $screen-lg) {
        left: auto;
        right: 0;
        width: auto;
        min-width: 180px;
      }
    }
  }

  &--error {
    color: $color-danger;

    #{$block-name}__name,
    #{$block-name}__select,
    #{$block-name}__help-text {
      color: $color-danger;
    }

    #{$block-name}__select,
    .choices__inner,
    .choices:after {
      border-color: $color-danger;
      background-color: lighten($color-danger, 35%);
    }
  }
}



/** @define choices */
.choices {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  line-height: 1.2;
  font-size: $font-size--sm;
  font-weight: 500;

  &:focus {
    outline: 0;

    #{$block-name}__inner {
      background-color: $gray-lightest;
    }
  }

  &:after {
    content: '';
    position: absolute;
    top: 16px;
    right: 25px;
    width: 9px;
    height: 9px;
    border-left: 2px solid $text-color;
    border-bottom: 2px solid $text-color;
    pointer-events: none;
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  &.is-open:after {
    transform: translateY(5px) rotate(135deg);
  }

  .is-hidden {
    display: none;
  }

  &__inner {
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: $field-padding-vertical 60px $field-padding-vertical $field-padding-horizontal;
    cursor: pointer;

    .is-open & {
      background-color: $gray-lightest;
    }

    #{$block-name}__item--selectable {
      max-width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &__list--dropdown {
    display: none;
    position: absolute;
    z-index: 2;
    top: calc(100% + 10px);
    left: 0;
    width: 100%;
    overflow: hidden;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    background-color: #fff;
    word-break: break-all;
    box-shadow: 0 6px 24px 0 rgba(0, 0, 0, 0.08);

    #{$block-name}__item {
      padding: 21px $field-padding-horizontal;
      cursor: pointer;

      &:not(:last-child) {
        border-bottom: 1px solid $gray-lighter;
      }

      &.is-highlighted {
        background: $gray-lightest;
      }
    }
  }

  &__heading {
    padding: $field-padding-vertical $field-padding-horizontal;
    font-size: $font-size--sm;
    font-weight: 700;
    opacity: 0.5;
    cursor: default;
  }

  &.is-open {

    #{$block-name}__list--dropdown {
      display: block;
    }
  }

  &.is-disabled {

    #{$block-name}__inner {
      background-color: $gray-lighter;
      cursor: default;
    }
  }
}
