// В этом файле должны быть стили для БЭМ-блока modal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.js-modal-open {
  overflow: hidden;
}

.modal {

  $block-name:                &; // #{$block-name}__element

  display: none;
  position: fixed;
  z-index: $z-index-modal;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;

  .js-modal-open & {
    overflow-x: hidden;
    overflow-y: auto;
  }

  &__dialog {
    flex-shrink: 0;
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;

    @media (min-width: $screen-md) {
      width: 720px;
      height: auto;
      margin: auto;
      box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.23);

      #{$block-name}--open & {
        animation: modal-content-show $transition-time 1 both;
      }
    }

    @media (min-width: $screen-lg) {
      width: 970px;
    }

    @media (min-width: $screen-xl) {
      width: 1170px;
    }
  }

  &__content {
    position: relative;
    min-height: 100%;
    padding: 10px;
    background-color: #fff;
    outline: 0;

    @media (min-width: $screen-md) {
      padding: 15px;
      border-radius: 8px;
      box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.23);
    }

    @media (min-width: $screen-lg) {
      padding: 36px 40px;
    }
  }

  &__close {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    border-radius: 8px;
    line-height: 30px;
    text-align: center;
    background: rgba(255, 255, 255, 0.5);
    cursor: pointer;

    svg {
      display: inline-block;
      vertical-align: middle;
    }
  }

  &__backdrop {
    display: none;
    position: fixed;
    z-index: ($z-index-modal - 1);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, $opacity);

    @media (min-width: $screen-lg) {
      animation: modal-backdrop-show $transition-time 1;
    }

    .js-modal-open & {
      display: block;
    }
  }

  &--open {
    display: flex;
  }
}



@keyframes modal-backdrop-show {

  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes modal-content-show {

  0% {
    opacity: 0;
    transform: translateY(-2em);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
