// В этом файле должны быть стили для БЭМ-блока popular, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.popular {

  $block-name:                &; // #{$block-name}__element

  &__inner {
    position: relative;
    padding: 10px;

    @media (min-width: $screen-md) {
      max-width: $screen-xl - 50;
      margin: auto;
      padding: 15px;
    }
  }

  &__title {
    margin: 0 0 30px;
    font-size: $font-size--h2 - 4;

    @media (min-width: $screen-md) {
      margin-bottom: 50px;
      font-size: $font-size--h2 + 4;
    }
  }

  &__slider {
    position: relative;
  }

  &__slider-btn {
    position: absolute;
    z-index: 2;
    top: 80px;
    border: none;
    padding: 0;
    width: 50px;
    height: 50px;
    background: none;
    cursor: pointer;

    &:hover,
    &:focus {
      outline: none;
    }

    svg {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      fill: $gray-light;
    }

    &--left {
      left: 0;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 37px;
        border: 2px solid $gray-light;
      }

      svg {
        left: 17px;
      }
    }

    &--right {
      right: 0;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 37px;
        border: 2px solid $gray-light;
      }

      svg {
        right: 16px;
      }
    }
  }
}
