// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.btn {
  box-sizing: border-box;
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  margin: 0;
  border: 1px solid transparent;
  border-radius: 28px;
  padding: 17px 66px;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  text-align: center;
  font-size: $font-size--h4;
  font-family: inherit;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  text-transform: none;
  color: #fff;
  background-color: $color-main;
  background-image: none;
  cursor: pointer;
  transition: all 0.3s;

  &:hover,
  &:focus {
    text-decoration: none;
    color: #fff;
    box-shadow: 0 6px 6px 0 rgba(47, 117, 236, 0.21);
  }

  &:active,
  &:focus {
    outline: none;
    background: $color-warning;
    box-shadow: 0 6px 6px 0 rgba(63, 128, 238, 0.21);
  }

  &--disabled,
  &:disabled {
    border: transparent;
    opacity: $opacity;
    cursor: not-allowed;
    color: #fff;
    background-color: $gray-lighten;
    box-shadow: none;

    @at-root a.btn--disabled {
      pointer-events: none;
    }

    &:hover {
      color: #fff;
      background-color: $gray-lighten;
    }
  }

  &__icon { // stylelint-disable-line
    display: inline-block;
    vertical-align: -0.05em;
    margin-right: 0.55em;
  }



  &--sm {
    padding: 15px 33px;
    font-size: $font-size;
    font-weight: 500;
  }

  &--gray {
    background: $gray-lightest;
    color: $text-color;
    border-color: $color-main;

    &:hover,
    &:focus {
      background-color: $color-main;
      color: #fff;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    }

    &:active {
      background-color: $color-main--light;
      color: #fff;
      box-shadow: 0 6px 6px 0 rgba(63, 128, 238, 0.21);
    }
  }

  &--text {
    border: none;
    padding: 0;
    color: $color-main;
    font-size: $font-size--h4;
    font-weight: 500;
    text-decoration: none;
    background: transparent;
    box-shadow: none;

    &:hover,
    &:focus,
    &:active {
      color: $color-main;
      text-decoration: underline;
      background: transparent;
      box-shadow: none;
    }

    &--disabled,
    &:disabled {
      opacity: 1;
      cursor: not-allowed;
      color: $color-main;
      text-decoration: underline;
      box-shadow: none;

      @at-root a.btn--disabled {
        pointer-events: none;
      }

      &:hover {
        color: $color-main;
        background: transparent;
      }
    }
  }

  &--price {
    padding: 16px 22px 16px 26px;
    font-size: $font-size--h4 + 2;
    box-shadow: 0 6px 18px 0 rgba(0, 0, 0, 0.12);

    &:focus,
    &:active {
      background: $color-main--light;
    }

    .btn__sm-text {
      position: relative;
      display: inline-block;
      padding-left: 42px;
      font-size: $font-size;
      font-weight: 500;

      &:before {
        content: '';
        position: absolute;
        top: -16px;
        bottom: -16px;
        left: 21px;
        width: 1px;
        background: $color-main--dark;
      }
    }
  }

  &--shadow {
    box-shadow: 0 6px 18px 0 rgba(0, 0, 0, 0.12);
  }

  // &--main {
