// В этом файле должны быть стили для БЭМ-блока page-footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-footer {
  background: #fff;

  .logo {
    align-self: center;
  }

  .social {
    margin-left: auto;
    align-self: center;
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 10px 0;

    @media (min-width: $screen-md) {
      max-width: $screen-xl - 50;
      margin: auto;
      padding: 20px 15px 0;
    }

    @media (min-width: $screen-xl) {
      padding-top: 55px;
    }
  }

  &__nav {
    width: 100%;
    padding: 0;
    margin: 16px 0;
    list-style: none;
    font-size: $font-size--sm;
    font-weight: 400;

    @media (min-width: $screen-md) {
      display: flex;
      flex-wrap: wrap;
    }

    @media (min-width: $screen-xl) {
      margin-top: 42px;
      margin-bottom: 56px;
    }

    a {
      color: $text-color;
      text-decoration: none;

      &:hover,
      &:focus {
        color: $text-color;
        text-decoration: none;
      }
    }
  }

  &__nav-item {

    @media (min-width: $screen-md) {
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }

    @media (min-width: $screen-lg) {
      margin-right: 42px;
    }

    &--muted {

      @media (min-width: $screen-md) {
        margin-right: auto;
      }

      @media (min-width: $screen-lg) {
        margin-right: auto;
      }

      a {
        color: $gray;
      }
    }
  }

  &__copyright {
    width: 100%;
    margin: 0;
    padding-top: 16px;
    padding-bottom: 20px;
    border-top: 1px solid $gray-lighter;
    text-align: center;
    font-size: $font-size--xs;
    color: $gray-light;
  }
}
