// В этом файле должны быть стили для БЭМ-блока product, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.product {

  $block-name:                &; // #{$block-name}__element

  width: 100%;

  @media (min-width: $screen-sm) {
    display: grid;
    align-items: start;
    grid-column-gap: 20px;
    grid-template-columns: 30% 1fr;
    grid-template-areas: 'img info' 'descr descr';
  }

  @media (min-width: $screen-lg) {
    grid-column-gap: 40px;
    grid-template-areas: 'img info' 'img descr';
  }

  @media (min-width: $screen-xl) {
    grid-template-columns: 360px 1fr;
    grid-template-rows: auto 1fr;
    grid-template-areas: 'img info' 'img descr';
  }

  &__img-wrap {
    grid-area: img;
    max-width: 211px;
    margin: 0 auto 30px;

    @media (min-width: $screen-sm) {
      max-width: 100%;
      margin: 0;
    }

    img {
      display: block;
    }
  }

  &__text-info {
    grid-area: info;

    @media (min-width: $screen-sm) {
      align-self: center;
    }

    @media (min-width: $screen-xl) {
      align-self: start;
    }
  }

  &__title {
    margin: 0 0 15px;
    font-size: $font-size--h2 - 4;
    font-weight: 900;

    @media (min-width: $screen-md) {
      margin: 0 0 30px;
      font-size: $font-size--h1;
    }
  }

  &__rating {
    margin-bottom: 10px;

    @media (min-width: $screen-md) {
      margin-bottom: 20px;
    }
  }

  &__table-info {
    width: 100%;
    border-collapse: collapse;

    @media (min-width: $screen-sm) {
      margin-bottom: 0;
    }

    @media (min-width: $screen-md) {
      font-size: $font-size--h4;
    }

    th {
      width: 160px;
    }

    th,
    td {
      padding: 0.3em;

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        padding-right: 0;
      }

      @media (min-width: $screen-md) {
        padding-top: 11px;
        padding-bottom: 11px;
      }
    }

    a {
      color: inherit;
      text-decoration: underline;

      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }

  &__subtitle {
    color: $text-color;

    @media (min-width: $screen-md) {
      margin-bottom: 15px;
      font-size: $font-size--h4;
    }

    @media (min-width: $screen-lg) {
      margin-top: 15px;
    }
  }

  &__descr {
    grid-area: descr;
    color: $gray-darken;
  }
}
