// В этом файле должны быть стили для БЭМ-блока page-header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {
  background-color: #fff;

  .main-nav__toggler {
    position: absolute;
    top: 10px;
    right: 10px;
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;

    @media (min-width: $screen-md) {
      padding: 15px;
    }

    @media (min-width: $screen-lg) {
      display: flex;
      flex-wrap: wrap;
      max-width: $screen-xl - 50;
      margin: auto;
    }

    @media (min-width: $screen-xl) {
      padding-top: 20px;
      // padding-bottom: 20px;
    }
  }

  &__nav {

    @media (min-width: $screen-lg) {
      margin-right: auto;
      margin-left: 50px;
    }
  }

  &__cart {
    position: relative;
    display: flex;
    width: 34px;
    height: 34px;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    text-decoration: none;
    color: $text-color;

    &:hover,
    &:focus {
      color: $text-color;
      text-decoration: none;
    }
  }

  &__cart-num {
    position: absolute;
    top: -2px;
    right: -3px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: $color-main;
    font-size: 10px;
    line-height: 20px;
    text-align: center;
    font-weight: 700;
    color: #fff;
  }

  &__user-auth {

    @media (min-width: $screen-lg) {
      display: flex;
      align-items: center;
      flex-direction: row-reverse;
      padding-left: 10px;

      .user-auth__avatar {
        margin-right: 0;
        margin-left: 13px;
      }
    }
  }

  &__book-tabs {
    width: calc(100% + 20px);
    border-top: 1px solid $gray-lighter;

    @media (min-width: $screen-md) {
      width: calc(100% + 30px);
    }

    @media (min-width: $screen-lg) {
      border-top: none;
    }

    &.tabs {
      margin: 20px -10px 0;
      padding-top: 20px;

      @media (min-width: $screen-md) {
        margin-right: -15px;
        margin-left: -15px;
      }

      @media (min-width: $screen-lg) {
        margin-top: 0;
        padding-top: 30px;
      }
    }
  }
}
