// В этом файле должны быть стили для БЭМ-блока catalog, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.catalog {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 40px;

  @media (min-width: $screen-md) {
    margin-bottom: 60px;
  }

  .pagination {
    margin-bottom: 0;
  }

  &__inner {
    position: relative;
    padding: 0 10px 10px;

    @media (min-width: $screen-md) {
      max-width: $screen-xl - 20;
      margin-left: auto;
      margin-right: auto;
      padding-right: 15px;
      padding-left: 15px;
    }
  }

  &__title {
    margin: 0;
    font-size: 0;
    color: transparent;
  }

  &__filters {
    padding-top: 10px;
    padding-right: 10px;
    padding-left: 10px;

    @media (min-width: $screen-md) {
      padding-top: 30px;
      padding-right: 15px;
      padding-left: 15px;
    }

    @media (min-width: $screen-lg) {
      padding-top: 40px;
    }
  }

  &__books-list {
    padding: 40px 10px 0;

    @media (min-width: $screen-sm) {
      display: flex;
      flex-wrap: wrap;

      .card {
        width: 50%;
      }
    }

    @media (min-width: $screen-md) {
      padding-top: 15px;

      .card {
        width: 33.333334%;
      }
    }

    @media (min-width: $screen-lg) {

      .card {
        width: 25%;
      }
    }

    @media (min-width: $screen-xl) {
      width: $container-xl;
      margin-left: auto;
      margin-right: auto;
      padding-left: 4px;
      padding-right: 4px;

      .card {
        width: 25%;
      }
    }
  }
}
