// В этом файле должны быть стили для БЭМ-блока field-radio, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field-radio {

  $block-name:                &; // #{$block-name}__element

  display: block;
  margin-bottom: $typo-margin-vertical;

  @media (min-width: $screen-md) {
    display: flex;
    flex-wrap: wrap;
  }

  &__title {
    @include field-name;
    margin-bottom: 14px;
    width: 100%;
  }

  &__input-wrap {

    &:not(:last-child) {

      @media (min-width: $screen-md) {
        margin-right: 38px;
      }
    }

    & + & {
      margin-top: 10px;

      @media (min-width: $screen-md) {
        margin-top: 0;
      }
    }

    &--error {

      #{$block-name}__name-text,
      #{$block-name}__help-text {
        color: $color-danger;
      }
    }
  }

  &__name {
    display: inline-block;
    position: relative;
    // margin-right: 0.6em;
    padding-left: 35px;
    font-weight: 400;
    line-height: $line-height;
  }

  &__name-text {
    display: inline-block;

    // своя радиокнопка без картинки

    @at-root input:disabled ~ & {
      opacity: 0.5;
      cursor: not-allowed;
    }

    &:before {
      content: '';
      position: absolute;
      top: 2px;
      left: 0;
      width: 20px;
      height: 20px;
      border: 1px solid $gray-lighten;
      border-radius: 50%;
    }

    @at-root input:checked ~ &:before {
      border-color: $color-warning;
    }

    @at-root input:focus ~ &:before {
      @include field-focus;
    }

    @at-root input:disabled ~ &:before {
      border-color: $gray-light;
      background: $gray-lighter;
    }

    &:after {
      content: '';
      position: absolute;
      top: 5px;
      left: 3px;
      width: 14px;
      height: 14px;
      opacity: 0;
      border-radius: 50%;
      background-color: $color-warning;
      transition: opacity $transition-time;

      @at-root input:checked ~ & {
        opacity: 1;
      }

      @at-root input:checked:disabled ~ & {
        background-color: $gray-light;
      }
    }

    // / своя радиокнопка без картинки
  }

  &__input {
    position: absolute;
    top: 0.8em;
    left: 0;
    margin: 0;
    padding: 0;
    transform: translateY(-50%);

    &:focus,
    &:active {
      @include field-focus;
    }

    // сокрытие инпута в случае использования своей радиокнопки
    opacity: 0;
  }

  &__help-text-wrap {
    padding-left: 1.5em;
  }

  &__help-text {
    @include field-help-text;
  }
}
