// В этом файле должны быть стили для БЭМ-блока field-price, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field-price {

  $block-name:                &; // #{$block-name}__element

  display: block;
  margin-bottom: $typo-margin-vertical;
  padding-top: 8px;

  &__name {
    @include field-name;
  }

  &__range {
    margin-top: 8px;
    margin-bottom: 16px;
  }

  &__input {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    max-width: 120px;
    // height: 2.125em;
    margin: 0 8px;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: $field-padding-vertical  $field-padding-horizontal;
    line-height: 1.2;
    font-size: $font-size--sm;
    font-weight: 500;
    font-family: inherit;
    background-color: #fff;
    text-align: center;
    appearance: textfield;

    &:focus {
      @include field-focus;
    }

    &[disabled],
    &[readonly] {
      cursor: not-allowed;
      background-color: $gray-lightest;
    }

    &[type='search']::-webkit-search-cancel-button,
    &[type='search']::-webkit-search-decoration {
      -webkit-appearance: none; // stylelint-disable-line  property-no-vendor-prefix
    }

    &[type='number']::-webkit-inner-spin-button,
    &[type='number']::-webkit-outer-spin-button {
      // height: auto;
    }

    &[type='color'] {
      // max-width: 100px;
      padding: 0;
    }

    &::placeholder {
      color: $text-color--muted;
      opacity: 1;
    }

    &:last-child {
      margin-right: 0;
    }

    @at-root textarea#{&} {
      $textarea-height: ($line-height * 2) + ($field-padding-vertical * 2);

      height: auto;
      min-height: calc(#{$textarea-height} + 2px);
      resize: vertical;
    }
  }

  &__inputs-wrap {
    display: flex;
    align-items: baseline;
  }

  &__dash {
    color: $text-color--muted;
  }



  &--sm {
    margin-bottom: 1em;
    font-size: $font-size--xs;

    @media (min-width: $screen-sm) {
      display: flex;
      align-items: flex-start;
    }

    #{$block-name}__name {
      color: $gray;
      font-size: $font-size--xs;
      font-weight: 500;
      text-transform: none;

      @media (min-width: $screen-sm) {
        flex-shrink: 0;
        width: 115px;
        margin-bottom: 0;
      }
    }

    #{$block-name}__input {
      max-width: 80px;
      font-size: $font-size--xs;
      padding: 9px 14px;
    }
  }
}


// stylelint-disable
/*! nouislider - 12.1.0 - 10/25/2018 */
.noUi-target,
.noUi-target * {
  touch-action: none;
  user-select: none;
}
.noUi-target {
  position: relative;
  direction: ltr;
}
.noUi-base,
.noUi-connects {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

.noUi-connects {
  overflow: hidden;
  z-index: 0;
}
.noUi-connect,
.noUi-origin {
  will-change: transform;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transform-origin: 0 0;
}

html:not([dir="rtl"]) .noUi-horizontal .noUi-origin {
  left: auto;
  right: 0;
}

.noUi-vertical .noUi-origin {
  width: 0;
}
.noUi-horizontal .noUi-origin {
  height: 0;
}
.noUi-handle {
  position: absolute;
}
.noUi-state-tap .noUi-connect,
.noUi-state-tap .noUi-origin {
  transition: transform 0.3s;
}
.noUi-state-drag * {
  cursor: inherit !important;
}

.noUi-horizontal {
  height: 6px;
}
.noUi-horizontal .noUi-handle {
  width: 16px;
  height: 16px;
  left: -17px;
  top: -6px;
  border-radius: 50%;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.24);
  outline: none;
}
.noUi-vertical {
  width: 18px;
}
.noUi-vertical .noUi-handle {
  width: 28px;
  height: 34px;
  left: -6px;
  top: -17px;
}
html:not([dir="rtl"]) .noUi-horizontal .noUi-handle {
  right: -8px;
  left: auto;
}

.noUi-target {
  background: $gray-lighter;
  border-radius: 3px;
}
.noUi-connect {
  background:$color-main;
}

.noUi-draggable {
  cursor: ew-resize;
}
.noUi-vertical .noUi-draggable {
  cursor: ns-resize;
}
.noUi-handle {
  border: 1px solid #D9D9D9;
  border-radius: 3px;
  background: #FFF;
  cursor: default;
  box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #EBEBEB, 0 3px 6px -3px #BBB;
}
.noUi-active {
  box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #DDD, 0 3px 6px -3px #BBB;
}

[disabled] .noUi-connect {
  background: #B8B8B8;
}
[disabled].noUi-target,
[disabled].noUi-handle,
[disabled] .noUi-handle {
  cursor: not-allowed;
}

.noUi-pips,
.noUi-pips * {
  box-sizing: border-box;
}
.noUi-pips {
  position: absolute;
  color: #999;
}

.noUi-value {
  position: absolute;
  white-space: nowrap;
  text-align: center;
}
.noUi-value-sub {
  color: #ccc;
  font-size: 10px;
}


.noUi-marker {
  position: absolute;
  background: #CCC;
}
.noUi-marker-sub {
  background: #AAA;
}
.noUi-marker-large {
  background: #AAA;
}


.noUi-pips-horizontal {
  padding: 10px 0;
  height: 80px;
  top: 100%;
  left: 0;
  width: 100%;
}
.noUi-value-horizontal {
  transform: translate(-50%, 50%);
}
.noUi-rtl .noUi-value-horizontal {

  transform: translate(50%, 50%);
}
.noUi-marker-horizontal.noUi-marker {
  margin-left: -1px;
  width: 2px;
  height: 5px;
}
.noUi-marker-horizontal.noUi-marker-sub {
  height: 10px;
}
.noUi-marker-horizontal.noUi-marker-large {
  height: 15px;
}


