// В этом файле должны быть стили для БЭМ-блока field-text, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field-text {

  $block-name:                &; // #{$block-name}__element

  display: block;
  margin-bottom: $typo-margin-vertical;

  &__name {
    @include field-name;
  }

  &__input-wrap {
    display: block;
  }

  &__input {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    // height: 2.125em;
    margin: 0;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: $field-padding-vertical  $field-padding-horizontal;
    line-height: 1.2;
    font-size: $font-size--sm;
    font-weight: 500;
    font-family: inherit;
    background-color: #fff;
    appearance: textfield;

    &:focus {
      @include field-focus;
    }

    &[disabled],
    &[readonly] {
      cursor: not-allowed;
      background-color: $gray-lightest;
    }

    &[type='search']::-webkit-search-cancel-button,
    &[type='search']::-webkit-search-decoration {
      -webkit-appearance: none; // stylelint-disable-line  property-no-vendor-prefix
    }

    &[type='number']::-webkit-inner-spin-button,
    &[type='number']::-webkit-outer-spin-button {
      // height: auto;
    }

    &[type='color'] {
      // max-width: 100px;
      padding: 0;
    }

    &::placeholder {
      color: $text-color--muted;
      opacity: 1;
    }

    @at-root textarea#{&} {
      $textarea-height: ($line-height * 2) + ($field-padding-vertical * 2);

      height: auto;
      min-height: calc(#{$textarea-height} + 2px);
      resize: vertical;
    }
  }

  &__help-text {
    @include field-help-text;
  }



  &--sm {
    margin-bottom: 1em;

    @media (min-width: $screen-sm) {
      display: flex;
      align-items: baseline;
    }

    #{$block-name}__name {
      color: $gray;
      font-size: $font-size--xs;
      font-weight: 500;
      text-transform: none;

      @media (min-width: $screen-sm) {
        flex-shrink: 0;
        width: 115px;
        margin-bottom: 0;
      }
    }

    #{$block-name}__input {
      font-size: $font-size--xs;
      padding: 9px 50px 9px 14px;
    }

    #{$block-name}__input-wrap {

      @media (min-width: $screen-sm) {
        flex-grow: 1;
      }
    }
  }

  &--bg-gray {

    #{$block-name}__input {
      background-color: $gray-lighter-2;

      &:focus {
        background-color: transparent;
      }
    }
  }

  &--input-checked {

    #{$block-name}__input {
      padding-right: 50px;
      background-image: svg-load('../blocks/field-text/bg-img/field-text-ok-bg.svg');
      background-position: right 22px center;
      background-repeat: no-repeat;
    }
  }


  &--input-error {

    #{$block-name}__input {
      padding-right: 50px;
      border-color: $color-danger;
      background-color: rgba(246, 63, 69, 0.04);
      background-image: svg-load('../blocks/field-text/bg-img/field-text-error-bg.svg');
      background-position: right 22px center;
      background-repeat: no-repeat;
    }
  }

  &--error {

    #{$block-name}__name,
    #{$block-name}__input,
    #{$block-name}__help-text {
      color: $color-danger;
    }

    #{$block-name}__input {
      border-color: $color-danger;
      background-color: lighten($color-danger, 35%);
    }
  }

  &--promocode {
    margin-bottom: 15px;
  }
}
