// В этом файле должны быть стили для БЭМ-блока filters, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.filters {

  $block-name:                &; // #{$block-name}__element

  position: relative;

  .field-text__input:not([disabled]) {
    background-color: transparent;
  }

  &__trigger {
    display: inline-block;
    margin-left: -8px;
    margin-bottom: 10px;
    border: 0;
    padding: 8px;
    background: none;
    cursor: pointer;

    @media (min-width: $screen-md) {
      margin-right: auto;
    }

    svg {
      vertical-align: -0.2em;
      width: 16px;
      height: 16px;
      margin-right: 12px;
    }
  }

  &__inner {
    max-width: $screen-xl - 80;
    margin: auto;
  }

  &__sorter {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    .field-select {

      @media (min-width: $screen-sm) {
        margin-right: auto;
      }

      @media (min-width: $screen-md) {
        margin-right: 30px;
      }
    }
  }

  &__view-thumb-btn,
  &__view-list-btn {
    display: none;
    cursor: pointer;

    @media (min-width: $screen-sm) {
      display: inline-block;
      width: 34px;
      height: 34px;
      margin-right: 26px;
      margin-bottom: 1em;
      border: 1px solid transparent;
      border-radius: $border-radius;
      padding: 0;
      background: none;
      line-height: 34px;

      &:last-child {
        margin-right: 0;
      }

      svg {
        display: inline-block;
        vertical-align: -0.2em;
      }
    }

    &:focus {
      @include field-focus;
    }
  }

  &__list-text {
    flex-shrink: 0;

    @media (min-width: $screen-lg) {
      width: 50%;
      padding-left: 14px;
      padding-right: 14px;
    }

    @media (min-width: $screen-xl) {
      width: 444px;
    }
  }

  &__list-data {
    flex-shrink: 0;

    @media (min-width: $screen-lg) {
      width: 50%;
      padding-left: 14px;
      padding-right: 14px;
    }

    @media (min-width: $screen-xl) {
      width: 414px;

      .field-select-group__name,
      .field-select__name,
      .field-price__name {
        width: 90px;
      }
    }
  }

  &__list-select-group {

    @media (min-width: $screen-md) {
      display: flex;
      // flex-wrap: wrap;
      margin-right: -14px;
      margin-left: -14px;

      .field-select {
        flex-grow: 1;
        min-width: 240px;
        padding-right: 14px;
        padding-left: 14px;
      }

      .field-select--lang {
        flex-grow: 1;
        min-width: 0;
      }
    }
  }

  &__result {
    width: 100%;
    padding-top: 5px;
    padding-bottom: 20px;
    text-align: center;

    @media (min-width: $screen-lg) {
      width: auto;
      padding-bottom: 1em;
      flex-grow: 1;
      align-self: flex-end;
    }
  }

  &__result-text {
    margin-top: 24px;
    margin-bottom: 24px;
    font-size: $font-size--h4;
    color: $color-main;

    @media (min-width: $screen-lg) {
      max-width: 240px;
      margin-right: auto;
      margin-left: auto;
    }
  }

  &__view-line {

    @media (min-width: $screen-md) {
      display: flex;
      align-items: baseline;
      padding-bottom: 25px;
    }
  }

  &__drop {

    @media (min-width: $screen-md) {
      padding-bottom: 20px;
    }

    @media (min-width: $screen-lg) {
      display: flex;
      flex-wrap: wrap;
      margin-left: -14px;
      margin-right: -14px;
    }

    .js & {
      height: 0;
      overflow: hidden;
      opacity: 0;

      @media (min-width: $screen-lg) {
        transition: opacity 0.2s;
      }
    }
  }

  &__trigger-icon-close {
    display: none;
  }

  &__trigger-icon-filter {
    display: inline-block;
  }



  &--open {
    background: #fff;

    &:before {
      content: '';
      position: absolute;
      z-index: -1;
      right: 0;
      bottom: 0;
      left: 0;
      height: 10px;
      box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.15);
    }

    #{$block-name}__trigger-icon-filter {
      display: none;
    }

    #{$block-name}__trigger-icon-close {
      display: inline-block;
    }

    #{$block-name}__drop {
      height: auto;
      opacity: 1;
      overflow: visible;

      @media (min-width: $screen-lg) {
        transition: opacity 0.4s;
      }
    }
  }
}
